main {
  width: 100%;
  margin: 10% auto;
}
@media (min-width: 71.25em) {
  main {
    width: 30%;
    margin: 10% auto;
  }
}

.card {
  background-color: var(--verydarkblue-card-bg);
  color: var(--white);
  border-radius: 0.9375rem;
  padding: 0.9375rem;
}
.card__image {
  position: relative;
  cursor: pointer;
  opacity: 1;
}
.card__image .main-img {
  display: block;
  max-width: 100%;
  height: auto;
  border-radius: 0.625rem;
  opacity: 1;
}
.card .overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: 0;
  transition: 0.3s ease;
  border-radius: 0.625rem;
}
.card .icon {
  color: var(--white);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}
.card .overlay:hover {
  background-color: rgba(0, 255, 247, 0.3);
  opacity: 1;
}
.card__name {
  color: var(--white);
  font-weight: 700;
  text-align: left;
  padding-top: 1.25rem;
}
.card__name:hover {
  color: var(--cyan);
  cursor: pointer;
}
.card__descript {
  font-weight: 300;
  color: var(--softblue);
  padding-top: 1.25rem;
  padding-bottom: 1.25rem;
}
.card__price {
  display: flex;
  color: var(--cyan);
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
  margin-bottom: -2.3125rem;
}
.card__price img {
  padding: 0.625rem;
}
.card__time {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: end;
  flex-wrap: nowrap;
  color: var(--softblue);
  width: 100%;
  margin-bottom: 0.9375rem;
}
.card__time img {
  padding: 0.625rem;
}
.card__creator {
  display: flex;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
  border-top: 0.125rem solid var(--verydarkblue-line);
  padding: 1.25rem 0 0.0625rem 0.3125rem;
  color: var(--softblue);
}
.card__creator img {
  border: 0.125rem solid var(--white);
  max-width: 100%;
  margin: 0.625rem;
  border-radius: 50%;
}
.card__creator span {
  color: var(--white);
  padding-left: 0.3125rem;
  text-decoration: none;
}
.card__creator span:hover {
  color: var(--cyan);
  cursor: pointer;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  background: var(--verydarkblue-main-bg);
  font-family: var(--font-outfit);
  font-size: 1.125rem;
}

:root {
  /* Primary Colors : */
  --softblue: hsl(215, 51%, 70%);
  --cyan: hsl(178, 100%, 50%);
  /* Neutral Colors : */
  --verydarkblue-main-bg: hsl(217, 54%, 16%);
  --verydarkblue-card-bg: hsl(217, 40%, 22%);
  --verydarkblue-line: hsl(215, 34%, 26%);
  --white: hsl(0, 0%, 100%);
}

:root {
  --font-outfit: "Outfit", sans-serif;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
}